@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

.app {
    display: flex;
    flex-direction: column;
    background: #333634;
    height: 100vh;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.clock {
    display: flex;
    flex-direction: row;
    color: #fff;

    & > div {
        font-size: 150px;
        margin: 5px;
    }
}

.date {
    color: #fff;
    font-size: 40px;
}